import React from 'react';
import './index.less';

/**
 * 
 * @param {object} style 
 * @param {number} total 
 * @param {number} current 
*/
export default (props) => {
  const { style = {}, total = 0, current = 0 } = props;
  const isWarn = current > total;
  return (
    <span style={style} className={`words-count ${isWarn ? 'warn' : ''}`}>{current}/{total}</span>
  );
}
